import React, { createContext, useContext, useState, useEffect } from 'react';
import { login, signup } from '../services/api';
import { AuthResponse, UserInfo } from '../types/api';
import { jwtDecode } from 'jwt-decode';

interface AuthContextType {
  isAuthenticated: boolean;
  user: UserInfo | null;
  setUser: (user: UserInfo) => void;
  login: (email: string, password: string, rememberMe?: boolean) => Promise<void>;
  signup: (data: SignupData) => Promise<void>;
  logout: () => void;
}

interface SignupData {
  firstname: string;
  lastname: string;
  email: string;
  password: string;
  acceptLegalPolicy: boolean;
}

interface JWTPayload {
  exp?: number;
  iat?: number;
  sub?: string;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [user, setUser] = useState<UserInfo | null>(null);

  const validateToken = (token: string): boolean => {
    try {
      const decoded = jwtDecode<JWTPayload>(token);
      if (!decoded.exp) return false;
      return decoded.exp > Date.now() / 1000;
    } catch {
      return false;
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('auth_token');
    localStorage.removeItem('user');
    setIsAuthenticated(false);
    setUser(null);
  };

  const handleSetUser = (newUser: UserInfo) => {
    setUser(newUser);
    localStorage.setItem('user', JSON.stringify(newUser));
  };

  useEffect(() => {
    const initializeAuth = () => {
      try {
        const token = localStorage.getItem('auth_token');
        const storedUser = localStorage.getItem('user');
        
        if (!token || !storedUser || !validateToken(token)) {
          handleLogout();
          return;
        }

        const parsedUser = JSON.parse(storedUser);
        setIsAuthenticated(true);
        setUser(parsedUser);
      } catch (error) {
        console.error('Auth initialization error:', error);
        handleLogout();
      }
    };

    initializeAuth();
  }, []);

  const handleLogin = async (email: string, password: string, rememberMe: boolean = false) => {
    try {
      const response: AuthResponse = await login(email, password, rememberMe);
      
      if (!response?.idToken || !validateToken(response.idToken)) {
        throw new Error('Invalid authentication response');
      }

      const userData = {
        ...response.user,
        email
      };

      localStorage.setItem('auth_token', response.idToken);
      localStorage.setItem('user', JSON.stringify(userData));
      
      setIsAuthenticated(true);
      setUser(userData);
    } catch (error) {
      handleLogout();
      throw new Error('Authentication failed. Please check your credentials.');
    }
  };

  const handleSignup = async (data: SignupData) => {
    try {
      const response: AuthResponse = await signup(data);
      
      if (!response?.idToken || !validateToken(response.idToken)) {
        throw new Error('Invalid signup response');
      }

      localStorage.setItem('auth_token', response.idToken);
      localStorage.setItem('user', JSON.stringify(response.user));
      
      setIsAuthenticated(true);
      setUser(response.user);

      return response;
    } catch (error) {
      handleLogout();
      throw new Error('Signup failed. Please try again.');
    }
  };

  const value = {
    isAuthenticated,
    user,
    setUser: handleSetUser,
    login: handleLogin,
    signup: handleSignup,
    logout: handleLogout
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};