import React from 'react';
import { X } from 'lucide-react';

interface BackgroundSelectorProps {
  isOpen: boolean;
  onClose: () => void;
  onSelect: (background: string) => void;
}

const backgrounds = [
  {
    id: 1,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/56.png',
    label: 'Stadium Night'
  },
  {
    id: 2,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/40.png',
    label: 'Stadium Day'
  },
  {
    id: 3,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/31.png',
    label: 'Pitch View'
  },
  {
    id: 4,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/30.png',
    label: 'Crowd'
  },
  {
    id: 5,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/26.png',
    label: 'Stadium Aerial'
  },
  {
    id: 6,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/24.png',
    label: 'Field Close-up'
  },
  {
    id: 7,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/13.png',
    label: 'Stadium Lights'
  },
  {
    id: 8,
    url: 'https://2points.fr/allsports/wp-content/uploads/2024/11/1-2.jpg',
    label: 'Evening Match'
  }
];

const BackgroundSelector: React.FC<BackgroundSelectorProps> = ({ isOpen, onClose, onSelect }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-zinc-900 rounded-xl p-6 w-full max-w-4xl">
        <div className="flex justify-between items-center mb-6">
          <h3 className="text-lg font-montserrat">Select Background</h3>
          <button 
            onClick={onClose}
            className="p-1 hover:bg-zinc-800 rounded-full transition-colors"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          {backgrounds.map((bg) => (
            <button
              key={bg.id}
              onClick={() => {
                onSelect(bg.url);
                onClose();
              }}
              className="group relative aspect-video rounded-lg overflow-hidden focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <img
                src={bg.url}
                alt={bg.label}
                className="w-full h-full object-cover transition-transform duration-300 group-hover:scale-110"
              />
              <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                <span className="text-white text-sm font-inter">{bg.label}</span>
              </div>
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default BackgroundSelector;